import React, { useState } from 'react';
const SpeakerCard = (props) => {
  console.log(props.speaker.firstName);

  return (
    <>
      <div className="card col-4">
        <img src={`/static/speakers/Speaker-${props.speaker.id}.jpg`} />
        <div>
          <h4 key={props.speaker.id}>{props.speaker.firstName}</h4>
          <p>{props.speaker.bioShort}</p>
        </div>
      </div>
    </>
  );
};

export default SpeakerCard;
